import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import ajax, { useFetcher } from 'shared/components/ajax';
import Navbar from "shared/components/ui/Navbar";
import { Box, Breadcrumbs, Button, FormControl, List, ListItem, ListItemText, TextField, Typography, withStyles } from "@material-ui/core";
import { Link, navigate } from "gatsby";
import { winalert } from "shared/components/util";
import MediaInput from "shared/components/ui/MediaInput";

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    head: {
        display: 'flex',
        alignItems: 'center',
    },
});

const Page = withStyles(styles)(({ classes }) => {
    const user = useUserInfo();
    const store = useFetcher(user && user.features.store && '/market/store/info');
    const [title, setTitle] = React.useState('');
    const [desc, setDesc] = React.useState('');
    const [price, setPrice] = React.useState(0);
    const [stock, setStock] = React.useState(0);
    const [galleries, setGalleries] = React.useState([]);
    React.useEffect(() => {
        if (user && !user.features.store) {
            winalert('Anda belum punya akun toko').then(() => navigate('/store/'));
        }
    }, [user]);
    const save = () => {
        ajax.post('/market/store/goods/', {
            title,
            desc,
            price,
            stock,
            galleries,
        }).then(() => {
            alert('Berhasil disimpan');
            navigate('/store/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        });
    }
    return (
        <>
            <Navbar />
            <Defer data={store}>
                {() => (
                    <List>
                        <ListItem>
                            <Breadcrumbs>
                                <Link color="inherit" to="/store/">
                                    Atur Toko
                                </Link>
                                <Link color="inherit" to="/store/goods/">
                                    Barang
                                </Link>
                                <Typography color="textPrimary">Tambah</Typography>
                            </Breadcrumbs>
                        </ListItem>
                        <ListItem>
                            <FormControl fullWidth>
                                <TextField label="Judul" variant="outlined" value={title} onChange={(e) => setTitle(e.target.value)} />
                            </FormControl>
                        </ListItem>
                        <ListItem>
                            <FormControl fullWidth>
                                <TextField multiline label="Deskripsi" variant="outlined" value={desc} onChange={(e) => setDesc(e.target.value)} />
                            </FormControl>
                        </ListItem>
                        <ListItem>
                            <FormControl fullWidth>
                                <TextField label="Harga" type="number" variant="outlined" value={price} onChange={(e) => setPrice(parseInt(e.target.value))} />
                            </FormControl>
                            <Box margin={1} />
                            <FormControl fullWidth>
                                <TextField label="Stok" variant="outlined" value={stock} onChange={(e) => setStock(parseInt(e.target.value))} />
                            </FormControl>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Galeri" />
                            <Box flexGrow={1} />
                            <MediaInput multiple value={galleries} onChange={setGalleries} />
                        </ListItem>
                        <ListItem>
                            <Box flexGrow={1} />
                            <Button onClick={save} disabled={!(title && desc)} variant="contained" color="primary">
                                Simpan
                            </Button>
                        </ListItem>
                    </List>
                )}
            </Defer>
        </>
    );
});

export default Page;
